body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --body-m: Mulish;
  --display-xs: Poppins;

  /* font sizes */
  --body-s-size: 14px;
  --body-m-size: 16px;
  --display-xs-size: 21px;
  --display-m-size: 38px;
  --display-l-size: 50px;
  --font-size-xs: 12px;
  --font-size-11xl: 30px;
  --display-s-size: 28px;
  --display-xl-size: 67px;

  /* Colors */
  --white: #fff;
  --color-black: #000;
  --cta-aqua-squeeze: #e5f4f2;
  --cta-button-pink: #ff8eb0;
  --deviders-grey: #d8d8d8;
  --neutral-700: #374151;
  --color-darkslategray-100: #3b3b3b;
  --onboarding-background-grey: #2d2d2d;
  --color-lavender: #dbe3ff;
  --neutral-zircon: #f8f9ff;
  --neutral-900: #111827;
  --accent-mandys-pink: #f2bfaf;

  /* Gaps */
  --gap-base: 16px;
  --gap-41xl: 60px;
  --gap-21xl: 40px;
  --gap-xl: 20px;
  --gap-5xs: 8px;
  --gap-3xs: 10px;
  --gap-xs: 12px;
  --gap-11xl: 30px;
  --gap-8xs: 5px;
  --gap-7xs: 6px;
  --gap-lg: 18px;
  --gap-5xl: 24px;
  --gap-31xl: 50px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-41xl: 60px;
  --padding-36xl: 55px;
  --padding-xl: 20px;
  --padding-11xl: 30px;
  --padding-base: 16px;
  --padding-31xl: 50px;
  --padding-5xs: 8px;
  --padding-101xl: 120px;
  --padding-21xl: 40px;
  --padding-181xl: 200px;
  --padding-lg: 18px;
  --padding-15xl: 34px;

  /* border radiuses */
  --br-xl: 20px;
  --br-3xs: 10px;
}
